import "./styles.css";

var container_div = document.getElementById("countThis");
var count = container_div.getElementsByTagName("div").length;
let img = document.getElementById("image");

let buttondown = document.getElementById("buttondown");
let buttonup = document.getElementById("buttonup");

buttondown.addEventListener("click", goDown);
buttonup.addEventListener("click", goUp);

function goDown() {
  for (let i = 0; i < count; i++) {
    if (document.getElementById(i).style.display != "none") {
      img.setAttribute("class", "rotated-image");
      setTimeout(function () {
        document.getElementById(i).setAttribute("style", "display:none");
      }, 1000);
      setTimeout(function () {
        img.setAttribute("class", "no-rot");
      }, 2000);
      break;
    }
  }
}
function goUp() {
  for (let i = 0; i < count; i++) {
    if (document.getElementById(i).style.display != "block") {
      img.setAttribute("class", "rotated-image");
      setTimeout(function () {
        document.getElementById(i).setAttribute("style", "display:block");
      }, 1000);
      setTimeout(function () {
        img.setAttribute("class", "no-rot");
      }, 2000);
      break;
    }
  }
}
